import {
    getShopClassService, getOneShopTitleService, queryPickupListService, getSkuTypeNumService,
    receiveBtnService, getCollectListService, saveCollectService, deleteCollectService, addGoodsService,
    getPromotionSkuInfoService
} from "../../services/goodsService";
import { mapActions, mapState } from "vuex";
import { Toast, Dialog} from "vant";
import SpeechRecognizerDialog from "../../common/currency/SpeechRecognizerDialog";
import Vue from 'vue';
import { store } from "../../utils/storage";

export const shopPage ={
    name: "shopPage",
    data() {
        return {
            isShowVoiceBtn: false, //语音搜索入口
            shopCode: "", //店铺CODE
            shopInfo: {
                shopLevel: 0
            }, //店铺信息
            pickupList: [],//店铺优惠券列表
            leftClassList: [{
                shopClassName: '',
                shopClassCode: ''
            }], //左侧分类列表
            goodsList: [], //右侧商品列表
            immediate: false,
            loading: false,//是否在加载中
            finished: false, //已经加载完毕全部商品
            goodsListHeight: 0, //右侧商品的最小高度
            bodyHeight: 0,
            flexFullHeight: 0,
            vanListHeight: 0,
            sideBarHeight: 0, //左侧分类列表的最小高度
            scrollTop: 0,//页面滑动高度
            activeKey: 0, //分类列表的索引值
            isSideBarFixed: false, //左侧分类列表是否悬浮
            isNavBar: true,  //顶部tabbar是否改变样式
            collectCode: "", //店铺收藏码
            isCollect: false, //店铺是否收藏
            icon: {
                backWhite: require("@/assets/images/common/nav_return_white.png"), //透明状态下 返回
                backBlack: require("@/assets/images/common/nav_return_black.png"), //透明状态下 返回
                collectionNor: require("@/assets/images/mall/mall_icon_collection_nor.png"), //透明状态下 未收藏
                collectionSel: require("@/assets/images/mall/mall_icon_collection_sel.png"), //透明状态下 收藏
                collectionBlackNor: require("@/assets/images/mall/mall_icon_collection_nor_black.png"), //不透明状态下 未收藏
                collectionBlackSel: require("@/assets/images/mall/mall_icon_collection_sel_red.png"), //不透明状态下 收藏
                shopCartWhite: require("@/assets/images/mall/mall_icon_shoppingcart_white.png"),//透明状态下 购物车
                shopCartBlack: require("@/assets/images/mall/mall_icon_shoppingcart_black.png"),//不透明状态下 购物车

                filter: require("@/assets/images/mall/icon_filter.png"),
                filterDown: require("@/assets/images/mall/icon_filter_down.png"),
                filterUp: require("@/assets/images/mall/icon_filter_up.png"),
            },
            skuTypeNum: 0,//购物车种类数量
            totalMoneyNum: 0,//购物车钱数
            lineMoneyNum: 0,//购物车划线数
            carriageNum: 0,//配送费
            sortMethod: "", // 0 销量 1 从小到大 2从大到小
            isPriceSort: false, // false 从小到大 true 从大到小
            // flag: -1,//0 从支付页面跳转的
            sendVal: false,
            recognizeResult: "正在聆听",
            scrollContentTop: "",
            sideBarTop: "",
            leftSideBarTop: "",
            isloading: false,  // 已经请求完毕
            rows: 10,
            changeInfoRows: 0,
            isShowCircle: false,
            addGoodsLoading: false,
            addBtnIcon: null,
            isScrollWrapper: false,
            showloading: '',

            isShowMaskBox: false, //显示蒙层
            isShowClassifyBox:false, //显示更多标签类别
            fullReductionList:[], //满减列表
            packList:[],//打包列表
            leftClassFlag:-1, //0 满减 1 打包 null 普通分类
            promotionGoodsList:[],//促销商品列表
            promotionClassList:[],//促销分类列表
            selectProClassIndex:0,//选中的促销分类
        }
    },
    created() {


    },
    mounted() {
        this.$nextTick(() => {
            this.bodyHeight = document.documentElement.clientHeight || document.body.clientHeight;
            console.log(this.$refs.flexFull.clientHeight);
            this.flexFullHeight = this.$refs.flexFull.clientHeight;
            this.goodsListHeight = this.bodyHeight - this.flexFullHeight;
            this.vanListHeight = this.goodsListHeight - this.$refs.classifyTitle.clientHeight - 43 - this.$stbHeight;
        })
    },
    activated() {
        this.isCollect = false; //不收藏
        if (this.$route.params.shopCode) {
            this.emptyHistory();
            this.shopCode = this.$route.params.shopCode;
            this.rows = 10;
            this.getShopClass();//获取左侧分类列表
            this.getOneShopTitle();//获取店铺信息
            this.queryPickupList();//获取店铺优惠券列表
            this.getCollectList(); //查询店铺是否被收藏
            if (this.$route.params.flag == 0) {
                this.setShopFlag(this.$route.params.flag);
            } else {
                this.setShopFlag(-1);
            }
            window.scrollTo(0, 0);
        } else {
            this.$refs.scrollContent.scrollTop = this.scrollContentTop;
            this.$refs.sideBar.scrollTop = this.sideBarTop;
            this.$refs.leftSideBar.$el.scrollTop = this.leftSideBarTop;
            this.changeInfoRows = this.goodsList.length
            this.getNewGoodsList();
        }


        this.paperScroll();
        this.getSkuTypeNum();
    },
    props: {},
    watch: {
        sortMethod: function (val) {
            if (val == "") {
                return;
            }
            this.$store.commit("shopGoodsPageNumberChange", {
                custPageNum: true,
                pageNumber: 1
            });
            this.rows = 10;
            this.getGoodsByShop(1, false);
        },
    },
    components: {
        'dialog-bar': SpeechRecognizerDialog
    },
    filters: {
        threshold(data) {
            return data >= 10000 ?  (data/10000>=10?'9万+':Math.floor(data / 10000) + '万+') : data
        },
        priceFilters(data){
            return data >= 10000 ? Math.floor(data/10000*100)/100+ '万' : data
        }
    },
    computed: mapState({
        userInfo: state => state.user.userInfo,
        pageNumber: state => state.productPage.shopGoodsPageNumber,
        shopFlag: state => state.productPage.shopFlag,
    }),
    methods: {
        ...mapActions(["getGoodsForShop","getNewGoodsForShop"]),
        //促销满减---销量排序
        promotionSaleSort(promotionGoodsItem){
            //sortNum 1 销量排序
            promotionGoodsItem.sortNum = 1;
            promotionGoodsItem.skus.sort(this.compare('saleMonthCount',false))
        },
        //促销满减---价格排序
        promotionPriceSort(promotionGoodsItem){
            //sortNum 2 价格升序排序 3 价格降序排序
            if(promotionGoodsItem.sortNum==2){
                promotionGoodsItem.sortNum = 3;
                promotionGoodsItem.skus.sort(this.compare('priceLow',false))
            }else{
                promotionGoodsItem.sortNum = 2;
                promotionGoodsItem.skus.sort(this.compare('priceLow',true))
            }
        },
        // 排序规则
        compare(attr,rev){
            //使用例子：newArray.sort(sortBy('number',false)) //表示根据number属性降序排列;若第二个参数不传递，默认表示升序排序
            //@param attr 排序的属性 如number属性
            //@param rev true表示升序排列，false降序排序
            //第二个参数没有传递 默认升序排列
            if(rev ==  undefined){
                rev = 1;
            }else{
                rev = (rev) ? 1 : -1;
            }
            return function(a,b){
                a = a[attr];
                b = b[attr];
                if(a < b){
                    return rev * -1;
                }
                if(a > b){
                    return rev * 1;
                }
                return 0;
            }
        },
        //点击促销分类
        setPromotionClassIndex(index){
            console.log(index)
            this.selectProClassIndex = index;
            if(!this.isSideBarFixed){
                this.$refs.scrollContent.scrollTop = this.$refs.sideBar.offsetTop-44;
            }
            console.log(this.$refs.reducedGoodsItem[index])
            console.log(this.$refs.reducedGoodsItem[index].offsetTop)
        },
        //显示优惠弹窗
        showMaskBox(){
            this.isShowMaskBox = !this.isShowMaskBox;
        },
        //隐藏优惠弹窗
        hideMaskBox(){
            this.isShowMaskBox = !this.isShowMaskBox;
        },
        setShowClassifyBox(){
            this.isShowClassifyBox = !this.isShowClassifyBox;
            if(!this.isSideBarFixed){
                this.$refs.scrollContent.scrollTop = this.$refs.sideBar.offsetTop-44;
            }
        },
        // 进入前状态
        beforeEnter(el) {
            console.log(this.addBtnIcon.getBoundingClientRect())
            el.style.left = this.addBtnIcon.getBoundingClientRect().left + 'px';
            el.style.top = this.addBtnIcon.getBoundingClientRect().top + 'px';
            el.style.transform = 'scale(1)'
        },
        // 进入中
        enter(el, done) {
            // el.offsetWidth 强制html渲染动画
            // el.offsetWidth 这句话如何不写就不会有动画效果直接渲染的
            el.offsetWidth;
            el.style.left = this.$refs.cartIcon.getBoundingClientRect().left + 'px'
            el.style.top = this.$refs.cartIcon.switchLeftClassgetBoundingClientRect().top + 'px'
            el.style.transform = 'scale(.5)'
            done()
        },
        afterEnter(el) {
            this.isShowCircle = !this.isShowCircle
        },
        // 清空历史记录
        emptyHistory() {
            this.activeKey = 0;
            this.sortMethod = '';
            this.isShowMaskBox = false; //显示蒙层
            this.isShowClassifyBox = false; //显示更多标签类别
            this.leftClassFlag = -1; //0 满减 1 打包 null 普通分类
            this.shopInfo = {
                shopLevel: 0
            }; //店铺信息
            this.pickupList = [],//店铺优惠券列表
                this.leftClassList = [{
                    shopClassName: '',
                    shopClassCode: ''
                }]; //左侧分类列表
            this.goodsList = []; //右侧商品列表
            this.isloading = false;

        },
        //设置店铺状态
        setShopFlag(shopFlag) {
            this.$store.commit("shopFlagChange", {
                custPageNum: true,
                shopFlag: shopFlag
            });
        },
        //语音搜索
        goToSpeech() {
            var _this = this;
            var ifly = cordova.plugins.IflyForXwjPlugin;
            _this.recognizeResult = "正在聆听";
            const platform = navigator.userAgent;
            const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
            if (isiOS) {
                _this.sendVal = true;
                ifly.startListen(function (result) {
                        if (result) {
                            var resultcatch = result.replace(/[\ |\~|\`|\!|\@|\#|\$|\%|\^|\&|\*|\(|\)|\-|\_|\+|\=|\||\\|\[|\]|\{|\}|\;|\:|\"|\'|\,|\<|\。|\>|\/|\？]/g, "");
                            _this.recognizeResult = resultcatch;
                            setTimeout(function () {
                                _this.sendVal = false;
                                console.log(resultcatch);
                                _this.goToGoodsSearch(resultcatch);
                            }, "2000");
                        } else {
                            _this.sendVal = false;
                            console.log('没有值')
                            Toast('我没听清您说什么')
                        }
                    },
                    function (message) {
                        _this.sendVal = false;
                        console.log("err" + message);
                    }, false, true
                );
            } else {
                var list = [
                    'android.permission.RECORD_AUDIO'
                ];
                var permissions = cordova.plugins.permissions;
                permissions.requestPermissions(list, function (status) {
                    if (status.hasPermission) {
                        _this.sendVal = true;
                        ifly.startListen(function (result) {
                                if (result) {
                                    console.log(result);
                                    var resultcatch = result.replace(/[\ |\~|\`|\!|\@|\#|\$|\%|\^|\&|\*|\(|\)|\-|\_|\+|\=|\||\\|\[|\]|\{|\}|\;|\:|\"|\'|\,|\<|\。|\>|\/|\？]/g, "");
                                    _this.recognizeResult = resultcatch;
                                    setTimeout(function () {
                                        _this.sendVal = false;
                                        _this.goToGoodsSearch(resultcatch);
                                    }, "2000");
                                } else {
                                    _this.sendVal = false;
                                    console.log('没有值')
                                    Toast('我没听清您说什么')
                                }
                            },
                            function (message) {
                                _this.sendVal = false;
                                console.log("err" + message);
                            }, false, true
                        );
                    }
                }, function (status) {
                    alert("请打开麦克风权限");
                });
            }
        },
        //价格排序
        priceSort() {
            if (!this.isPriceSort) {
                this.sortMethod = 1;
            } else {
                this.sortMethod = 2;
            }
            this.isPriceSort = !this.isPriceSort;
        },
        //页面的滑动
        paperScroll() {
            this.scrollTop = this.$refs.scrollContent.scrollTop;
            let offsetTop = this.$refs.sideBar.offsetTop;
            var platformHeight=0;
            //安卓下添加容错高度
            if (this.$devicePlatform == 'ios') {
                var platformHeight=0;
            }else{
                var platformHeight=2;
            }
            // console.log(this.scrollTop,offsetTop-this.flexFullHeight-2)
            if (this.scrollTop >= offsetTop - this.flexFullHeight - platformHeight) {
                this.$refs.scrollContent.scrollTop = offsetTop - this.flexFullHeight;
                this.isSideBarFixed = true;
                this.isScrollWrapper = true;
                setTimeout(() => {
                    this.isScrollWrapper = false;
                }, 100)
            } else {
                this.isSideBarFixed = false;
                this.$refs.sideBar.scrollTop = 0;
            }
            console.log("===========");
            console.log(this.scrollTop, offsetTop - this.flexFullHeight - 2, this.isSideBarFixed)
        },
        //商品列表的滑动
        classifyScroll() {
            //如果是满减、打包分类，不需要加载更多数据
            if(this.leftClassFlag==0||this.leftClassFlag==1){
                return;
            }
            let scrollTop = this.$refs.sideBar.scrollTop;
            let clientHeight = this.$refs.vanList.clientHeight;

            if (clientHeight - scrollTop - this.goodsListHeight < 600 && !this.loading) {
                this.onLoadGoodsList();
            }
        },
        handleTouchStart: function (e) {
            this.startPoint = e.touches[0].pageY;
        },

        handleTouchMove: function (e) {
            let poChange = e.changedTouches[0].pageY - this.startPoint;
            // this.pointChange = poChange
            // console.log(poChange)
        },
        handleTouchEnd: function (e) {
            if(this.leftClassFlag == 0||this.leftClassFlag == 1){
                return
            }
            let goodsListTop = this.$refs.sideBar.scrollTop;
            let goodsBantListHeight = this.$refs.sideBar.scrollHeight;
            let end = e.changedTouches[0].pageY;
            let start = this.startPoint;
            let delta = start - end;
            if (this.isSideBarFixed) {
                console.log(goodsListTop + this.goodsListHeight + 10, goodsBantListHeight);
                if ((goodsListTop + this.goodsListHeight + 10 >= goodsBantListHeight && this.finished) || (this.goodsList.length == 0 && this.isloading)) {
                    if (delta > 150) {
                        if (this.activeKey != this.leftClassList.length - 1) {
                            this.showloading = store.get("showLoadingView");
                            if (this.showloading) {
                                this.showloading(true);
                            }
                            setTimeout(() => {
                                if (this.showloading) {
                                    this.showloading(false);
                                }
                                this.activeKey = this.activeKey + 1;
                                if (this.activeKey >= 9) {
                                    this.$refs.leftSideBar.$el.scrollTop = this.$refs.leftSideBar.$el.clientHeight;
                                }
                                this.switchLeftClass(this.activeKey);
                            }, 1000)
                        }
                    }
                }
                // if(goodsListTop <= 0){
                //     if (delta < -150) {
                //         console.log(delta)
                //         // if(this.secondCateIndex!=0){
                //         //     this.secondCateIndex = this.secondCateIndex-1;
                //         //     this.changeSecondMoreList(this.secondCateIndex);
                //         // }
                //     }
                // }
            }


        },
        /**
         * 查询店铺是否被收藏
         * @params
         * collectType String  店铺收藏标识
         * contentCode String  店铺CODE
         * userCode    Number
         */
        getCollectList() {
            let paramData = {
                collectType: "02",
                contentCode: this.shopCode,
                userCode: this.$userInfo.OWNER_CODE
            }
            getCollectListService(paramData).then(res => {
                if (res.resultCode == "0") {
                    if (res.data.size > 0) {
                        this.collectCode = res.data.list[0].collectCode;//收藏码
                        this.isCollect = true; //收藏
                    } else {
                        this.isCollect = false; //不收藏
                    }

                }
            }).catch(error => {
            })
        },
        //更改收藏状态
        collectBtn() {
            if (this.isCollect) {
                //取消收藏
                this.isCollect = false; //收藏
                this.deleteCollect();
            } else {
                //收藏
                this.isCollect = true; //收藏
                this.saveCollect();
            }
        },
        /**
         * 收藏该店铺
         * @param
         * collectType  String  收藏店铺的标识
         * contentCode  Number  店铺CODE
         * userCode     Number  业主CODE
         */
        saveCollect(isFirst = true) {
            let paramData = {
                collectType: "02",
                contentCode: this.shopCode,
                userCode: this.$userInfo.OWNER_CODE
            }
            saveCollectService(paramData).then(res => {
                if (res.resultCode == 0) {
                    this.collectCode = res.data.collectCode;
                    this.isCollect = true;
                } else {
                    if (isFirst) {
                        this.saveCollect(false)
                    } else {
                        this.isCollect = false; //不收藏
                    }
                }
            }).catch(error => {
            });
        },
        /**
         * 取消该店铺的收藏
         * @param
         * collectCodes  Arr  店铺的收藏码
         */
        deleteCollect(isFirst = true) {
            let paramData = {
                collectCodes: [this.collectCode]
            }
            deleteCollectService(paramData).then(res => {
                if (res.resultCode == 0) {
                    this.isCollect = false;
                } else {
                    if (isFirst) {
                        this.deleteCollect(false);
                    } else {
                        this.isCollect = true; //收藏
                    }
                }
            }).catch(error => {
            });
        },

        /**
         * 查询购物车数量
         * ownerCode  Number  业主CODE
         */
        getSkuTypeNum() {
            let paramData = {
                ownerCode: this.$userInfo.OWNER_CODE
            }
            getSkuTypeNumService(paramData).then(res => {
                if (res.resultCode == 0) {
                    this.skuTypeNum = res.data.skuTypeNum;
                }
            }).catch(error => {
            })
        },
        //点击左边列表切换分类
        switchLeftClass(index) {
            this.activeKey = index;
            this.$refs.sideBar.scrollTop = 0;
            this.leftClassFlag = this.leftClassList[this.activeKey].actFlag;
            if(this.leftClassList[this.activeKey].actFlag==0||this.leftClassList[this.activeKey].actFlag==1){
                this.selectProClassIndex = 0;
                this.getPromotionSkuInfo();
                return
            }
            this.sortMethod = "";
            this.isPriceSort = false;
            if (this.leftClassList[this.activeKey].goodsList == null) {
                this.rows = 10;
                this.goodsList = [];
                this.finished = true;
                this.isloading = false;
                this.getGoodsByShop(1, false)
            } else {
                this.$store.commit("shopGoodsPageNumberChange", {
                    custPageNum: true,
                    pageNumber: this.leftClassList[this.activeKey].pageNumber
                });
                // this.rows = this.leftClassList[this.activeKey].rows;
                this.goodsList = this.leftClassList[this.activeKey].goodsList;
                this.finished = this.leftClassList[this.activeKey].finished;
                // this.$el.querySelector('#classifyList').scrollIntoView({block: "start", behavior: "smooth"})
                // this.$el.querySelector('#classifyList').scrollIntoView(true)
                // this.$refs.scrollContent.scrollTop = this.$refs.sideBar.offsetTop-46;

            }

        },
        /**
         * 获取店铺促销商品列表
         * @param
         * shopCode  Number      店铺CODE
         * areaCode  Number      小区编码
         * actType   Number      活动类型
         * ownerCode Number      业主编码
         **/
        getPromotionSkuInfo(){
            let paramData ={
                shopCode:this.shopCode,
                areaCode:this.$userInfo.AREA_CODE,
                actType:this.leftClassFlag,
                ownerCode:this.$userInfo.OWNER_CODE,
            }
            getPromotionSkuInfoService(paramData).then(res => {
                if (res.resultCode == 0) {
                    // console.log(res)
                    let rulesTxt = "";
                    res.data.map((item)=>{
                        item.sortNum = 0;
                        if(item.fullReductionRules){
                            item.fullReductionRules.map((rulesItem,rulesIndex)=>{
                                if(rulesIndex == item.fullReductionRules.length-1){
                                    rulesTxt+='满'+rulesItem.ruleFull+'减'+rulesItem.ruleReduction;
                                    return
                                }
                                rulesTxt+='满'+rulesItem.ruleFull+'减'+rulesItem.ruleReduction+'，';
                            })
                            item.rulesTxt = rulesTxt;
                            rulesTxt = "";
                        }
                    })
                    this.promotionGoodsList = res.data;
                    console.log(this.promotionGoodsList)
                }
            }).catch(error => {
            });
        },
        /**
         * 获取店铺左侧分类列表
         * @param
         * shopCode  Number      店铺CODE
         **/
        getShopClass() {
            let paramData = {
                shopCode: this.shopCode,
                areaCode:this.$userInfo.AREA_CODE,
            }
            getShopClassService(paramData).then(res => {
                if (res.resultCode == 0) {
                    res.data.map((item) => {
                        console.log(item.shopClassName.length)

                        if (item.shopClassName.length > 5) {

                            item.shopClassName = item.shopClassName.substring(0, 5);

                        }
                    })

                    this.leftClassList = res.data;
                    this.getGoodsByShop(1)
                }
            }).catch(error => {
            });
        },
        /**
         * 获取店铺对应分类的商品列表
         * @param
         * shopCode  Number      店铺CODE
         * shopClassCode Number  对应分类的CODE
         * areaCode Number       小区CODE
         * page Number           页数
         * rows Number           个数
         *
         * isFrist  第一次显示
         **/
        getGoodsByShop(pageNumber, isFrist = true) {
            let leftIndex = this.activeKey;
            let rows = this.rows;
            let paramData = {
                shopCode: this.shopCode,
                shopClassCode: this.leftClassList[leftIndex].shopClassCode,
                areaCode: this.$userInfo.AREA_CODE,
                ownerCode: this.$userInfo.OWNER_CODE,
                page: pageNumber,
                rows: this.rows,
                sortMethod: this.sortMethod
            }
            this.getGoodsForShop(paramData).then(res => {
                if (res.resultCode == "0") {
                    if (res.data.list.length != 0) {
                        res.data.list.map((item, index) => {
                            item.skuShopNum = 0;
                            if (index % 2 == 0) {
                                item.spuPicUrl = item.spuPicUrl.replace(/xwjfile.juhaolian.cn/, 'xwjfilea.juhaolian.cn')
                            }
                            Vue.prototype.$isHasLocalImage(item.spuPicUrl, function (localImgUrl) {
                                item.spuPicUrl = localImgUrl;
                            })
                        })
                    }
                    this.leftClassList[leftIndex].goodsList = res.data.list;
                    this.leftClassList[leftIndex].pageNumber = 2;
                    // this.leftClassList[leftIndex].rows = rows;
                    this.leftClassList[leftIndex].finished = !res.data.hasNextPage;
                    this.goodsList = res.data.list;
                    this.isloading = true;
                    this.loading = false;
                    this.finished = !res.data.hasNextPage;
                }else
                {
                    Dialog.alert({
                        message: res.msg
                    }).then(() => {
                        // on confirm
                        this.goBack();
                    })
                    this.goodsList = [];
                    this.isloading = true;
                    this.loading = false;
                    this.finished = true;
                }
                if (!isFrist) {
                    // this.$refs.scrollContent.scrollTop = this.$refs.sideBar.offsetTop-46;
                    // this.$el.querySelector('#classifyList').scrollIntoView(true)
                    // this.$el.querySelector('#classifyList').scrollIntoView({block: "start", behavior: "smooth"})
                }
                this.$store.commit("shopGoodsPageNumberChange", {
                    custPageNum: true,
                    pageNumber: 2
                });
            }).catch(error => {
            });
        },
        getNewGoodsList() {
            let leftIndex = this.activeKey;
            // let rows = this.rows;
            let paramData = {
                shopCode: this.shopCode,
                shopClassCode: this.leftClassList[leftIndex].shopClassCode,
                areaCode: this.$userInfo.AREA_CODE,
                ownerCode: this.$userInfo.OWNER_CODE,
                page: 1,
                rows: this.changeInfoRows,
                sortMethod: this.sortMethod
            }
            this.getNewGoodsForShop(paramData).then(res => {
                if (res.resultCode == "0") {
                    this.leftClassList[leftIndex].goodsList.map((item, index) => {
                        res.data.list.map((resItem) => {
                            if (item.spuCode == resItem.spuCode) {
                                item.skuList[0].skuPrice = resItem.skuList[0].skuPrice;
                                item.cartCount = resItem.cartCount;
                                item.storeNumber = resItem.storeNumber;
                                this.goodsList[index].skuList[0].skuPrice = resItem.skuList[0].skuPrice;
                                this.goodsList[index].cartCount = resItem.cartCount;
                                this.goodsList[index].storeNumber = resItem.storeNumber;
                            }
                        })

                    })
                    // this.leftClassList[leftIndex].goodsList = res.data.list;
                    // this.leftClassList[leftIndex].pageNumber = 2;
                    // this.leftClassList[leftIndex].rows = rows;
                    // this.leftClassList[leftIndex].finished = !res.data.hasNextPage;
                    // this.goodsList = this.leftClassList[leftIndex].goodsList;
                    // this.isloading = true;
                    // this.loading = false;
                    // this.finished = !res.data.hasNextPage;
                }
                // this.$store.commit("shopGoodsPageNumberChange", {
                //     custPageNum: true,
                //     pageNumber: 2
                // });
                this.$nextTick(() => {

                })
            }).catch(error => {
            });
        },
        /**
         * 上拉加载更多商品
         * @param
         * shopCode  Number      店铺CODE
         * shopClassCode Number  对应分类的CODE
         * areaCode Number       小区CODE
         * page Number           页数`
         * rows Number           个数
         **/
        onLoadGoodsList() {
            if (this.finished) {
                return
            }
            this.loading = true;
            let leftIndex = this.activeKey;
            let paramData = {
                shopCode: this.shopCode,
                shopClassCode: this.leftClassList[leftIndex].shopClassCode,
                areaCode: this.$userInfo.AREA_CODE,
                page: this.pageNumber,
                rows: this.rows,
                sortMethod: this.sortMethod
            }
            this.getGoodsForShop(paramData).then(res => {
                if (res.resultCode == "0") {
                    if (res.data.list.length != 0) {
                        res.data.list.map((item, index) => {
                            item.skuShopNum = 0;
                            if (index % 2 == 0) {
                                item.spuPicUrl = item.spuPicUrl.replace(/xwjfile.juhaolian.cn/, 'xwjfilea.juhaolian.cn')
                            }
                            Vue.prototype.$isHasLocalImage(item.spuPicUrl, function (localImgUrl) {
                                item.spuPicUrl = localImgUrl;
                            })
                        })
                        this.leftClassList[leftIndex].pageNumber = this.pageNumber;
                    }
                    this.leftClassList[leftIndex].goodsList = this.leftClassList[leftIndex].goodsList.concat(res.data.list);
                    this.leftClassList[leftIndex].finished = !res.data.hasNextPage;
                    this.goodsList = this.leftClassList[leftIndex].goodsList;
                    this.loading = false;
                    this.finished = !res.data.hasNextPage;
                }
            }).catch(error => {
            });
        },
        /**
         * 获取店铺信息
         * @param
         * shopCode  Number      店铺CODE
         **/
        getOneShopTitle() {
            let paramData = {
                shopCode: this.shopCode,
                areaCode: this.$userInfo.AREA_CODE
            }
            getOneShopTitleService(paramData).then(res => {
                if (res.resultCode == 0) {
                    this.shopInfo = res.data
                    if (res.data.shopStatus != 1) {
                        this.showAlert(res.msg);
                    }
                }
            }).catch(error => {
            });
        },
        showAlert(msg) {
            Dialog.alert({
                message: msg
            }).then(() => {
                // on confirm
                this.goBack();
            })
        },
        /**
         * 获取店铺优惠券列表
         * @param
         * shopCode  Number      店铺CODE
         * ownerCode Number      用户CODE
         * areaCode  Number      小区CODE
         **/
        queryPickupList() {
            let paramData = {
                shopCode: this.shopCode,
                ownerCode: this.$userInfo.OWNER_CODE,
                areaCode: this.$userInfo.AREA_CODE
            }
            queryPickupListService(paramData).then(res => {
                if (res.resultCode == 0) {
                    this.pickupList = res.data.date

                }
            }).catch(error => {
            });
        },
        /**
         * 领取优惠券
         * @param
         * id        Number      优惠券CODE
         * ownerCode Number      用户CODE
         * areaCode  Number      小区CODE
         * roomCode  Number      房间CODE
         **/
        receiveBtn(pickupItem) {
            if (!this.$userInfo.ROOM_CODE) {
                Dialog.confirm({
                    message: "您未绑定房间，是否前往绑定？",
                    confirmButtonText: "前往"
                }).then(() => {
                    //跳转到绑定房产页面
                    this.$router.push("selectArea");
                }).catch(() => {
                });
                return
            }
            let paramData = {
                id: pickupItem.id,
                ownerCode: this.$userInfo.OWNER_CODE,
                areaCode: this.$userInfo.AREA_CODE,
                roomCode: this.$userInfo.ROOM_CODE
            }
            receiveBtnService(paramData).then(res => {
                if (res.resultCode == 0) {
                    Toast('领取优惠券成功');
                    pickupItem.isPicked = 1;
                } else {
                    Toast(res.msg)
                }
            }).catch(error => {
            });
            //
        },

        //点击加号，增加商品数量
        addGoodsNum(goodsItem, goodsIndex) {
            // if (goodsItem.storeNumber == 0) {
            //     Toast('该商品正在努力补货中哦')
            //     return;
            // }
            if (this.addGoodsLoading || this.isShowCircle) {
                return;
            }
            this.addBtnIcon = this.$refs.addBtn[goodsIndex];
            this.isShowCircle = true;
            this.addGoodsLoading = true;
            let paramData = {
                ownerCode: this.$userInfo.OWNER_CODE,
                spuCode: goodsItem.spuCode,
                skuNo: goodsItem.skuList[0].skuNo,
                shopCode: this.shopCode,
                activitySpuId: goodsItem.activitySpuId,
                skuCount: 1,
                mark: 1,
            }
            addGoodsService(paramData).then(res => {
                if (res.resultCode == "0") {
                    Toast({
                        message: '添加购物车成功',
                        duration: 1000
                    });
                    goodsItem.cartCount += 1;

                    this.getSkuTypeNum();
                }else if (res.resultCode == '20100' && res.data.result == '0') {
                    this.$toast('您所添加商品超过最大购买量了哦');
                }
                this.addGoodsLoading = false;
            }).catch(error => {
                this.addGoodsLoading = false;
            })
            // if (goodsItem.skuShopNum < goodsItem.skuList[0].storeNumber) {
            //     if (goodsItem.skuShopNum < goodsItem.skuCountLimit) {
            //         goodsItem.skuShopNum++;
            //         this.totalMoneyNum += goodsItem.skuList[0].skuPrice;
            //         if (goodsItem.priceLine == "") {
            //             this.lineMoneyNum += goodsItem.skuList[0].skuPrice;
            //         } else {
            //             this.lineMoneyNum += Number(goodsItem.priceLine);
            //         }
            //
            //     } else {
            //         Toast("超过限购数量");
            //     }
            // } else {
            //     Toast("库存不足");
            // }
        },
        //点击减号，减少商品数量
        reduceGoodsNum(goodsItem) {
            if (goodsItem.skuShopNum > 0) {
                goodsItem.skuShopNum--;
                this.totalMoneyNum -= goodsItem.skuList[0].skuPrice;
                if (goodsItem.priceLine == "") {
                    this.lineMoneyNum -= goodsItem.skuList[0].skuPrice;
                } else {
                    this.lineMoneyNum -= Number(goodsItem.priceLine);
                }

            }
        },
        //去结算订单
        goToSettleAccounts() {
            this.settleAccountsList = [];
            let skuList = [];

            for (let i = 0; i < this.leftClassList.length; i++) {
                if (this.leftClassList[i].goodsList != null) {
                    for (var j = 0; j < this.leftClassList[i].goodsList.length; j++) {
                        if (this.leftClassList[i].goodsList[j].skuShopNum != 0) {
                            skuList.push({
                                spuName: this.leftClassList[i].goodsList[j].spuName,
                                spuCode: this.leftClassList[i].goodsList[j].spuCode,

                                skuNo: this.leftClassList[i].goodsList[j].skuList[0].skuNo,
                                attrValueContent: this.leftClassList[i].goodsList[j].skuList[0].attrValueContent,
                                skuPrice: this.leftClassList[i].goodsList[j].skuList[0].skuPrice,
                                skuCount: this.leftClassList[i].goodsList[j].skuShopNum,
                                skuDelivery: this.leftClassList[i].goodsList[j].skuDelivery,
                                skuTotal: this.leftClassList[i].goodsList[j].skuShopNum * this.leftClassList[i].goodsList[j].skuList[0].skuPrice,
                                skuPicUrl: this.leftClassList[i].goodsList[j].skuList[0].skuPicUrl
                            })

                        }
                    }
                }
            }

            if (skuList.length > 0) {

                this.settleAccountsList.push({
                    shopName: this.shopInfo.shopName,
                    shopCode: this.shopInfo.shopCode,
                    shopTotal: this.totalMoneyNum,
                    skuList: skuList
                });
                console.log('$scope.settleAccountsList' + JSON.stringify(this.settleAccountsList));

                let goodsData = {
                    orderSource: '1',
                    goodsList: this.settleAccountsList,
                    num: Math.random() * 100
                }
                this.$router.push({
                    name: "orderPayNew",
                    params: {
                        'goodsData': goodsData
                    }
                });
            } else {
                Toast("您还未选择商品");
            }
        },
        //返回上一页
        goBack() {
            this.activeKey = 0;
            this.sortMethod = '';
            if (this.shopFlag == 0) {
                this.$router.push({
                    name: 'productPage',
                    params: {}
                })
            } else {
                this.$router.go(-1);
            }
        },
        //去搜索页面
        goToGoodsSearch(speechTxt) {
            this.$router.push({
                name: 'goodsSearch',
                params: {
                    shopCode: this.shopCode,
                    speechTxt: speechTxt,
                    isRefresh: 1,
                }
            })
        },
        //去购物车页面
        goToShoppingCart() {
            this.$router.push({
                name: 'shoppingCart',
                params: {}
            })
        },
        //去商品详情页
        goToProduct(goodsItem) {
            if (goodsItem.activitySpuId) {
                this.$router.push({
                    name: 'flashSaleGoods',
                    query: {
                        spuCode: goodsItem.spuCode,
                        activitySpuId: goodsItem.activitySpuId,
                        goodsUrl: goodsItem.spuPicUrl
                    }
                })
            } else {
                this.$router.push({
                    name: 'goodsDetail',
                    params: {
                        spuCode: goodsItem.spuCode,
                        goodsUrl: goodsItem.spuPicUrl
                    }
                })
            }

        },

    },
    beforeRouteLeave(to, form, next) {
        console.log("-----")
        console.log()
        this.scrollContentTop = this.$refs.scrollContent.scrollTop;
        this.sideBarTop = this.$refs.sideBar.scrollTop;
        this.leftSideBarTop = this.$refs.leftSideBar.$el.scrollTop;
        next();
    }
}

